import { useState, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { toast } from 'react-toastify';

import { contactsOperations, contactsSelectors } from '../../redux/contacts';

import ButtonContact from '../ButtonContact';

import styles from './ContactForm.module.scss';

const initialState = {
  name: '',
  number: '',
};

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç —Ñ–æ—Ä–º—ã –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞
export default function ContactForm() {
  const [state, setState] = useState(initialState);
  const { name, number } = state;

  const contacts = useSelector(contactsSelectors.getContacts); // –°–µ–ª–µ–∫—Ç–æ—Ä –≤—Å–µ—Ö –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
  const isLoading = useSelector(contactsSelectors.getLoading); // –°–µ–ª–µ–∫—Ç–æ—Ä —Å—Ç–∞—Ç—É—Å–∞ –∑–∞–≥—Ä—É–∑–∫–∏

  const dispatch = useDispatch();

  // –î–∏—Å–ø–∞—Ç—á–∏—Ç –æ–ø–µ—Ä–∞—Ü–∏—é –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞ + useCallback
  const onSubmit = useCallback(
    (name, number) => {
      dispatch(contactsOperations.addContact(name, number));
    },
    [dispatch],
  );

  // –°–ª–µ–¥–∏—Ç –∑–∞ –∏–Ω–ø—É—Ç–æ–º
  const hanldeChange = e => {
    const { name, value } = e.currentTarget;

    setState(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  // –ú–µ—Ç–æ–¥ –Ω–∞ –æ—Ç–ø—Ä–∞–≤–∫–µ —Ñ–æ—Ä–º—ã
  const hanldeSubmit = e => {
    e.preventDefault();

    const normalizedName = name.toLowerCase();

    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –¥—É–±–ª–∏–∫–∞—Ç –ø–æ –∏–º–µ–Ω–∏
    const nameInContacts = contacts.find(
      contact => contact.name === normalizedName,
    );

    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –¥—É–±–ª–∏–∫–∞—Ç –ø–æ –Ω–æ–º–µ—Ä—É
    const numberInContacts = contacts.find(
      contact => contact.number === number,
    );

    // –û—Ç–ø—Ä–∞–≤–∫–∞ –∏–º–µ–Ω–∏ –∏ –Ω–æ–º–µ—Ä–∞ –ø–æ—Å–ª–µ –ø—Ä–æ–≤–µ—Ä–∫–∏
    if (!nameInContacts && !numberInContacts) {
      onSubmit(normalizedName, number); // –í—ã–∑–æ–≤ –æ–ø–µ—Ä–∞—Ü–∏–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞

      resetForm();
      return;
    }

    toast.warn(`üòÆ ${name} is already in contacts`);
  };

  // –°–±—Ä–æ—Å –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã (–ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏)
  const resetForm = () => {
    setState(initialState);
  };

  return (
    <form className={styles.form} onSubmit={hanldeSubmit}>
      <label className={styles.label}>
        <span className={styles.text}>Name</span>

        <input
          type="text"
          name="name"
          placeholder="Enter name"
          aria-label="Input your name"
          className={styles.input}
          value={name}
          onChange={hanldeChange}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          disabled={isLoading}
          required
        />
      </label>

      <label className={styles.label}>
        <span className={styles.text}>Number</span>

        <input
          type="tel"
          name="number"
          placeholder="Enter phone number"
          aria-label="Input your phone number"
          className={styles.input}
          value={number}
          onChange={hanldeChange}
          pattern="(\+?( |-|\.)?\d{1,2}( |-|\.)?)?(\(?\d{3}\)?|\d{3})( |-|\.)?(\d{3}( |-|\.)?\d{4})"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ 11-12 —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã, –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –ø—É–∑–∞—Ç—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          disabled={isLoading}
          required
        />
      </label>

      <ButtonContact />
    </form>
  );
}
