import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import Alert from '@material-ui/lab/Alert';

import ContactForm from '../../components/ContactForm';
import Filter from '../../components/Filter';
import ContactsList from '../../components/ContactsList';
import Loader from '../../components/Loader';

import { contactsOperations, contactsSelectors } from '../../redux/contacts';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
export default function PhoneBookView() {
  const dispatch = useDispatch();

  const isLoadingContacts = useSelector(state =>
    contactsSelectors.getLoading(state),
  ); // –°–µ–ª–µ–∫—Ç–æ—Ä —Å—Ç–∞—Ç—É—Å–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –∏–∑ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
  const isError = useSelector(state => contactsSelectors.getError(state)); // –°–µ–ª–µ–∫—Ç–æ—Ä —Å—Ç–∞—Ç—É—Å–∞ –æ—à–∏–±–∫–∏ –∏–∑ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤

  // –§–µ—Ç—á –∑–∞ –∫–æ–Ω—Ç–∞–∫—Ç–∞–º–∏ –ø—Ä–∏ –º–∞—É–Ω—Ç–µ
  useEffect(() => {
    dispatch(contactsOperations.fetchContacts());
  }, [dispatch]);

  // –°–µ—Ç–∏—Ç title —Å—Ç—Ä–∞–Ω–∏—Ü—ã –ø—Ä–∏ –º–∞—É–Ω—Ç–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  useEffect(() => {
    document.title = 'Contacts list | Phonebook';
  }, []);

  return (
    <main>
      <ContactForm />

      <Filter />

      <ContactsList />

      {isLoadingContacts && <Loader />}

      {isError && <Alert severity="error">{isError}</Alert>}
    </main>
  );
}
