import { useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { contactsSelectors, contactsActions } from '../../redux/contacts';

import styles from './Filter.module.scss';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç —Ñ–∏–ª—å—Ç—Ä–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
export default function Filter() {
  const value = useSelector(contactsSelectors.getFilter); // –°–µ–ª–µ–∫—Ç–æ—Ä –∑–Ω–∞—á–µ–Ω–∏—è —Ñ–∏–ª—å—Ç—Ä–∞
  const isLoading = useSelector(contactsSelectors.getLoading); // –°–µ–ª–µ–∫—Ç–æ—Ä —Å—Ç–∞—Ç—É—Å–∞ –∑–∞–≥—Ä—É–∑–∫–∏

  const dispatch = useDispatch();

  // –î–∏—Å–ø–∞—Ç—á–∏—Ç –≤ —ç–∫—à–Ω –∑–Ω–∞—á–µ–Ω–∏–µ –∏–Ω–ø—É—Ç–∞
  const onChange = useCallback(
    e => {
      dispatch(contactsActions.changeFilter(e.currentTarget.value));
    },
    [dispatch],
  );

  return (
    <div className={styles.container}>
      <h2 className={styles.title}>Contacts</h2>

      <label className={styles.label}>
        <span className={styles.text}>Find contacts by name</span>

        <input
          type="text"
          name="filter"
          placeholder="Find"
          className={styles.input}
          value={value}
          onChange={onChange}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="Find contacts by name"
          disabled={isLoading}
          required
        />
      </label>
    </div>
  );
}
